import React, { useState, useEffect } from "react";
import { useHistory } from 'react-router-dom';
import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Image from "react-bootstrap/Image";
import axios from 'axios';
import "../../App.css";

export default function Login() {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [now, setNow] = useState(true);
  const [user, setUser] = useState("");

  const history = useHistory();

  useEffect(() => {
    const loggedInUser = sessionStorage.getItem("user");
    if (loggedInUser) {
      const foundUser = JSON.parse(loggedInUser);
      setUser(foundUser);
    }
  }, []);

  // logout the user
  

  // login the user
  const handleSubmit = async e => {
    e.preventDefault();
    const response = await axios.get('https://blooming-island-42972.herokuapp.com/users/');

    for(var i = 0; i < response.data.length; i++) {
      if (username === response.data[i].name && password === response.data[i].password) {
        setUser(response.data[i]);
        sessionStorage.setItem("user", JSON.stringify(response.data[i]));
        break;
      }
    }
  };


  if (user) {
    let path = `/chap`;
    history.push(path);
    window.location.reload();
  }

  function validateForm() {
    return username.length > 0 && password.length > 0;
  }


  return (
    <Container className="mx-auto" style={{ width: "300px" }}>
      <br />
      <Image className ="logoHome" src="logo.png" />
      <Form className="" onSubmit={handleSubmit} >
        <br />
        <Form.Group controlId="email" >
          <Form.Control
            type="username"
            placeholder="Username"
            value={username}
            onChange={(e) => setUsername(e.target.value)} />
        </Form.Group>
        <Form.Group controlId="password">
          <Form.Control
            type="password"
            placeholder="Password"
            value={password}
            onChange={(e) => setPassword(e.target.value)} />
        </Form.Group>
        <Button variant="primary" type="submit" disabled={!validateForm()}>
          Login
        </Button>

      </Form>
    </Container>
  );
}